body {
  background-color: #E3F6F5;
  color: #1E1E1E; }

.wrapper {
  width: 300px;
  height: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 150px auto 0; }

h1 {
  font-size: 50px;
  line-height: 60px;
  margin-bottom: 20px; }

span {
  margin-bottom: 20px;
  font-size: 25px;
  line-height: 35px; }

a {
  font-size: 18px;
  line-height: 20px;
  text-decoration: none;
  color: #1E1E1E;
  cursor: pointer;
  border-bottom: 1px solid #1E1E1E; }

